.App{
  width: 100vw;
  min-height: 100vh;
  background-color: #434343;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.SearchForm{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 10vh;
  background-color: #434343;
  top:0;
  left:50%;
  transform: translateX(-50%);
}

.SearchInput{
  width: 40vw;
  height: 5vh;
  padding-left: 1vw;
  border: none;
  outline: none;
}
.SearchButton{
  background-color: #a64452;
  color:#f8f8f8;
  margin-left: 1rem;
  padding: 0 1rem;
  height: 5vh;
  border:none;
  outline: none;
  transition: 1s;
}

.SearchButton:hover{
  background-color: #f8f8f8;
  color:#a64452;
}



.Recipe{
  height: 25vw;
  width: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f7c873;
  color:#434343;
  border-radius: 1rem;
  transition: 1s ease;
}
.Recipe:hover{
  box-shadow: 2px 2px 5px 5px grey;
}


.Recipe h1{
  text-align: center;
  font-size: 1.5rem;
  width: 80%;
  max-height: 8vw;
  overflow: hidden;
}
.Recipe img{
  width: 15vw;
  height: 15vw;
  padding-bottom: 1vh;
}


.RecipesContainer{
  background-color: #c7c7c7;
  width: 96vw;
  display: grid;
  justify-content: space-around;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap:3rem;
  padding-top: 12vh;
  padding-bottom: 2rem;
  padding-left: 2vw;
  padding-right: 2vw;

}
@media(max-width:1200px){
  .RecipesContainer{
    grid-template-columns: 1fr 1fr;
  }
}


@media(max-width:700px){
  .RecipesContainer{
    grid-template-columns: 1fr;
  }
  .Recipe{
    flex-direction: row;
    height: 30vh;
    width: 90vw
  }
  .Recipe h1{
    max-height: 20vh;
    width: 70%;
  }
}